    <div class="analyze-pic-content">
      <el-row :gutter="20">
        <el-col :span="11" :offset="1">
          <el-card shadow="always" :body-style="{ padding: '20px' }">
            <div slot="header">
              <div class="header-wrapper">
                <div class="header-left">
                  <div class="header-left-up" style="padding-bottom: 10px">
                    {{ $t("analyze.salesRanking") }}
                  </div>
                  <div class="header-left-down">{{ $t("analyze.topTen") }}</div>
                </div>
                <div class="header-right">
                  <el-select
                    v-model="saleType"
                    value-key=""
                    placeholder=""
                    clearable
                    filterable
                    style="width: 100px"
