                      :value="item.value"
                    >
                    </el-option>
                  </el-select>
                </div>
              </div>
            </div>
            <!-- card body -->
            <div
              class="analyze_no_data"
              v-show="incomeSheet.length > 0 ? false : true"
            >
              {{ $t("analyze.noData") }}
            </div>
            <v-chart
              v-show="incomeSheet.length > 0 ? true : false"
              :forceFit="true"
              :height="height"
              :data="incomeSheet"
              :scale="zhexianscale"
            >
              <v-tooltip />
              <v-axis />
              <v-line position="name*price" :color="color" />
              <v-point position="name*price" shape="circle" :color="color" />
            </v-chart>
          </el-card>
