    </div>
    <div class="analyze-operator-action">
      <el-button type="primary" size="default" @click="download">{{
        $t("btn.download")
      }}</el-button>
      <el-select
        v-model="TaxShowTime"
        value-key=""
        clearable
        filterable
        style="width: 100px"
        @change="changeTaxTime"
      >
        <el-option
          v-for="item in taxOptions"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        >
        </el-option>
      </el-select>
    </div>
    <div class="analyze-excel-content">
      <div class="analyze-loading" v-show="loading"></div>
      <div v-show="!loading" class="analyze-content-wrapper">
        <el-table :data="excelData" border stripe>
          <el-table-column :label="$t('analyze.orderTime')" align="center">
            <template slot-scope="{ row }">
              <span>{{ $d(new Date(row.order_date), "long") }}</span>
            </template>
          </el-table-column>
          <el-table-column
            v-for="col in tablecols"
            :prop="col.id"
            :key="col.id"
            :label="col.label"
            :width="col.width"
            align="center"
          >
          </el-table-column>
        </el-table>
      </div>
      <pagination
        :total="ordersTotal"
        :page.sync="listQuery.page"
        :limit.sync="listQuery.limit"
        @pagination="getList"
      ></pagination>
    </div>
  </el-card>
</template>

<script>
import { downloadExcel } from "@/util/excel.js";
import { getCommodityList } from "@/api/userAPI.js";

const scale = [
  {
    dataKey: "sales",
  },
  {
    dataKey: "name",
    min: 0,
    max: 1,
  },
];

const zhexianscale = [
  {
    dataKey: "price",
    min: 0,
  },
  {
    dataKey: "name",
    min: 0,
    max: 1,
  },
];
import {
  getExcelData,
  getIncomeOrder,
  getAnalyzeInfo,
  test,
} from "@/api/analyzeAPI";
import { getProductStatistics } from "@/api/statisticsAPI";
import { mapState } from "vuex";
import Pagination from "@/components/Pagination";
import { startLoading, endLoading } from "@/util/loading.js";
export default {
  created() {
    test();
    let orderTime = this.$t("analyze.orderTime");
    let orderId = this.$t("analyze.orderId");
    let orderTotalPrice = this.$t("analyze.orderTotalPrice");
    let taxRant = this.$t("analyze.taxRant");
    let tax = this.$t("analyze.tax");
    let sheetData = [
      {
        [orderTime]: 123,
        [orderId]: 2,
        [orderTotalPrice]: 12,
        [taxRant]: "0.8%",
        [tax]: 12,
      },
    ];
    console.log(sheetData);
    // downloadExcel(sheetData);
    getAnalyzeInfo().then((ret) => {
      this.analyzeInfo = ret.data;
    });
    getCommodityList();
  },
  mounted() {
    this.getTaxData();
    this.getProductData();
    this.getIncome();
  },
  data() {
    return {
      analyzeInfo: {
        today_order_total_price: 0,
        total_order_price: 0,
        today_invalid_orders: 0,
        today_valid_orders: 0,
        total_valid_orders: 0,
        total_invalid_orders: 0,
        yesterday_order_total_price: 0,
        last_week_order_total_price: 0,
        this_week_order_total_price: 0,
      },
      color: "#1fab89",
      scale,
      zhexianscale,
      height: 300,
      saleType: "week",
      incomeType: "day",
      TaxShowTime: "day",
      dayIncomeOrder: [],
      monthIncomeOrder: [],
      salesOptions: [
        { label: this.$t("select.day"), value: "day" },
        { label: this.$t("select.week"), value: "week" },
        { label: this.$t("select.month"), value: "month" },
      ],
      incomeOptions: [
        { label: this.$t("select.day"), value: "day" },
        { label: this.$t("select.month"), value: "month" },
      ],
      taxOptions: [
        { label: this.$t("select.day"), value: "day" },
        { label: this.$t("select.week"), value: "week" },
        { label: this.$t("select.month"), value: "month" },
      ],

      tablecols: [
        { label: this.$t("analyze.orderId"), id: "id" },
        { label: this.$t("analyze.orderTotalPrice"), id: "order_total_price" },
        { label: this.$t("analyze.orderReceivePrice"), id: "order_price" },
        { label: this.$t("analyze.taxRant"), id: "tax_rate" },
        { label: this.$t("analyze.tax"), id: "tax" },
      ],
      excelData: [],
      ordersTotal: 0,
      productData: [],
      sheetData: [
        {
          [this.$t("analyze.orderTime")]: 123,
          [this.$t("analyze.orderId")]: 2,
          [this.$t("analyze.orderTotalPrice")]: 12,
          [this.$t("analyze.taxRant")]: "0.8%",
          [this.$t("analyze.tax")]: 12,
        },
      ],
      dayOrderMap: {
        1: this.$t("week.monday"),
        2: this.$t("week.tuesday"),
        3: this.$t("week.wednesday"),
        4: this.$t("week.thursday"),
        5: this.$t("week.friday"),
        6: this.$t("week.saturday"),
        7: this.$t("week.sunday"),
      },
      monthOrderMap: {
        1: this.$t("month.jan"),
        2: this.$t("month.feb"),
        3: this.$t("month.mar"),
        4: this.$t("month.apr"),
        5: this.$t("month.may"),
        6: this.$t("month.june"),
        7: this.$t("month.july"),
        8: this.$t("month.aug"),
        9: this.$t("month.sep"),
        10: this.$t("month.oct"),
        11: this.$t("month.nov"),
        12: this.$t("month.dec"),
      },
      listQuery: {
        limit: 10,
        offset: 0,
      },
      loading: false,
    };
  },
  methods: {
    isDeclineRate(data1, data2) {
      if (data2 == 0) {
        return false;
      }
      return (data1 - data2) / data2 < 0;
    },
    orderPriceRate(data1, data2) {
      if (data2 == 0 && data1 == 0) {
        return 0;
      }
      if (data2 == 0) {
        return 100;
      }
      return Math.abs(((data1 - data2) / data2) * 100);
    },
    changeSaleType() {
      this.getProductData();
    },
    changeIncomeType() {
      this.getIncome();
    },
    changeTaxTime() {
      this.getTaxData();
    },
    compare(property) {
      return function(obj1, obj2) {
        let value1 = obj1[property];
        let value2 = obj2[property];
        return value1 - value2;
      };
    },
    getIncome() {
      let obj = {
        shop_id: this.shopId,
        way: this.incomeType,
      };
      if (this.incomeType) {
        getIncomeOrder(obj)
          .then((data) => {
            if (this.incomeType === "day") {
              let obj = data.data.sort(this.compare("day"));
              this.dayIncomeOrder = obj;
            } else {
              this.monthIncomeOrder = data.data;
            }
          })
          .catch(() => {
            this.$message.error(this.$t("error.serverErr"));
          });
      }
    },
    getProductData() {
      let obj = {
        shop_id: this.shopId,
        type: 1,
        way: this.saleType,
      };
      if (this.saleType) {
        getProductStatistics(obj)
          .then((data) => {
            let productData = data.data.reduce((acu, cur) => {
              acu.push({ name: cur.name, sales: cur.sales });
              return acu;
            }, []);
            console.log(productData);
            this.productData = productData;
          })
          .catch(() => {
            this.$message.error(this.$t("error.serverErr"));
          });
      }
    },
    getList() {
      this.getTaxData();
    },
    getTaxData() {
      this.loading = true;
      startLoading(".analyze-loading");
      let obj = {
        method: this.TaxShowTime,
        limit: this.listQuery.limit,
        offset: this.listQuery.offset,
      };
      if (this.TaxShowTime) {
        getExcelData(obj)
          .then((ret) => {
            this.loading = false;
            setTimeout(() => {
              endLoading();
            }, 0);
            this.excelData = ret.data.data;
            this.ordersTotal = ret.data.total;
          })
          .catch((err) => {
            console.log(err);
            this.$message.error(this.$t("error.serverErr"));
            setTimeout(() => {
              endLoading();
            }, 0);
          });
      }
    },
    download() {
      let obj = {
        method: this.TaxShowTime,
        limit: 9999,
        offset: 0,
      };
      this.$message({
        message: this.$t("message.excelCreate"),
        type: "info",
      });
      getExcelData(obj)
        .then((ret) => {
          let newExcelDataList = ret.data.data.reduce((acu, cur) => {
            acu.push({
              [this.$t("analyze.orderTime")]: this.$d(
                new Date(cur.order_date),
                "long"
              ),
              [this.$t("analyze.orderId")]: cur.id,
              [this.$t("analyze.orderTotalPrice")]: cur.order_total_price,
              [this.$t("analyze.orderReceivePrice")]: cur.order_price,
              [this.$t("analyze.taxRant")]: cur.tax_rate,
              [this.$t("analyze.tax")]: cur.tax,
            });
            return acu;
          }, []);
          downloadExcel(newExcelDataList, this.$t("analyze.statistics"));
          setTimeout(() => {
            this.$message({
              message: this.$t("message.excelCreate"),
              type: "info",
            });
          }, 0);
        })
        .catch(() => {
          this.$message.error(this.$t("message.excelFailed"));
        });
    },
  },
  computed: {
    incomeSheet() {
      if (this.incomeType === "day") {
        return this.dayIncomeOrder.reduce((acu, cur) => {
          acu.push({ name: this.dayOrderMap[cur.day], price: cur.price });
          return acu;
        }, []);
      } else {
        return this.monthIncomeOrder.reduce((acu, cur) => {
          acu.push({ name: this.monthOrderMap[cur.month], price: cur.price });
          return acu;
        }, []);
      }
    },

    ...mapState({
      shopId: (state) => state.user.loginData.id,
    }),
  },
  components: {
    Pagination,
  },
};
</script>

