// 年月收入排行
export function getIncomeOrder(params) {
  const url = "statistics_income_order";
  const ret = parseObjectToGetUrl(params);
  return $axios.get(url + ret);
}

// 获取分析数据
export function getAnalyzeInfo() {
  const url = "get_analyze_data/v1";
  return $axios1.get(url);
}
